html {
  scroll-snap-type: y mandatory;
  overflow-x: hidden;
}

body {
  --scroll-snap-type: y mandatory;
}

.App {
  --overflow: hidden;
  --position: relative;
  color: #737373;
}

@media only screen and (max-width: 1023px) {
  section #inicio {
    scroll-snap-align: center;
    scroll-snap-stop: always;
  }

  #xdd {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    scroll-margin-bottom: 1000px;
  }
}
